import logo from "../images/Logo1.png";

import { Link } from "react-router-dom";
import { pages } from "../pages/Router";
import { ButtonAnimation } from "./animation/Animation";

export default function Navbar() {
  const defaultPage = pages.find((page) => page.default);

  return (
    <div className="container">
      <header className="d-flex flex-wrap align-items-center justify-content-center justify-content-md-between py-3 mb-4 border-bottom">
        <div className="col-md-2 mb-2 mb-md-0">
          <ButtonAnimation>
            <Link
              to={defaultPage ? defaultPage.path : "/"}
              className="d-inline-flex link-body-emphasis text-decoration-none"
              style={{ position: "relative" }}
            >
              <img src={logo} alt="logo" className="logo"></img>
              <p className="alpha_text">ALPHA</p>
            </Link>
          </ButtonAnimation>
        </div>

        <ul className="nav col-12 col-md-8 mb-2 d-flex justify-content-evenly mb-md-0">
          {pages.map((page, i) => (
            <li key={i}>
              <ButtonAnimation>
                <Link to={page.path} className="nav-link px-2 link-secondary">
                  {page.name}
                </Link>
              </ButtonAnimation>
            </li>
          ))}
        </ul>

        <div className="col-md-2 text-end">
          {/* <button type="button" className="btn btn-outline-primary me-2">Login</button>
          <button type="button" className="btn btn-primary">Sign-up</button> */}
        </div>
      </header>
    </div>
  );
}
