import { AnimatePresence } from "framer-motion";
import React from "react";
import { Navigate, Route, Routes, useLocation } from "react-router-dom";
import Body from "../components/Body";
import Home from "../components/Home";
import NotFound from "../components/NotFound";
import { Page } from "../interfaces/app.interfaces";

export const pages: Page[] = [
  { name: "Home", path: "/Home", element: <Home />, default: true },
  { name: "Body", path: "/Body", element: <Body /> },
].map((page, index) => ({
  ...page,
  index: index,
}));

export default function Router() {
  const location = useLocation(); // The useLocation hook will assist the routes in determining the page we are on.

  return (
    <React.Fragment>
      <AnimatePresence mode="wait">
        <Routes key={location.pathname} location={location}>
          {pages.map((page, index) => {
            return (
              <React.Fragment key={index}>
                {page.default && (
                  <Route
                    key={"default"}
                    path="/"
                    element={<Navigate to={page.path} replace />}
                  />
                )}
                <Route key={index} path={page.path} element={page.element} />
              </React.Fragment>
            );
          })}

          <Route path="*" element={<NotFound />} />
        </Routes>
      </AnimatePresence>
    </React.Fragment>
  );
}
